import React from 'react';
import PropTypes from 'prop-types';

class NewApp extends React.Component{
    constructor(props){
        super(props);
        console.log("constructor");
    }
    state = {
        count : 0
    };
    //add = () => this.setState({count: this.state.count+1});
    // this.state.count 는 멋지지 않으므로 다음과 같은 코드를 사용한다.
    add = () => this.setState(current => ({count: current.count+1}));
    minus = () => this.setState(current => ({count: current.count-1}));
    //state에는 바뀌어야할 데이터를 넣는다. (class에서 사용가능)
    //state안의 값을 변경할때는 반드시 render를 호출하는 함수를 추가해줘야함.!! setState가 자동으로 render호출해줌.
    componentDidMount(){
        console.log("componnet rendered");
    }
    componentDidUpdate(){
        console.log("componet updated");
    }
    componentWillUnmount(){
        console.log("component is dead");
    }
    render(){
        console.log("componnet rendering!");
    return <div>
        <h1>number is : {this.state.count}</h1>
        <button onClick={this.add}>add</button>
        <button onClick={this.minus}>minus</button>
        </div>;
    }
}
//react 는 자동적으로 classcomponent 의 render method를 실행함.자동으로!
export default NewApp; 