import React from 'react';// 이거 선언안하며 밑에 return <div>이것들 이해 못함
import PropTypes from 'prop-types';
function Food({name, rating}){//저 밑에서 선언한 prop를 가져올수 있다.
  // props.fav === {fav} 이다.
  //{fav} 는 props.fav를 nice하게 가져오기위한 방법.
  //console.log(prop); {}이게 props라고 생각하면 편함
return <div><h4>{rating}/5.0</h4><h1>hey i'm {name}</h1></div>
}
//props들은 해당 jsx의 argument로 간다. 

//food fav를 자동으로 추가하기 위하여 배열을 만든다
//문제.. propTypes에 string을 추가하였는데 any타입은 number, string 안가린다.
const foodLike = [{id: 1 ,name:"치킨",rating: 5 },{id: 2 ,name:"피자",rating: 4.5},{id:3,name:"커피",rating:3}];
//id는 react오류를 위해 추가한것인데 key prop에 넣어줘서 각 component는 유니크하다고 선언해줘야한다고함.
function App() {//jsx는 대문자로시작
  return (
    <div className="App">
      
        hello;
        {foodLike.map(renderFood)}
      
    </div>
  );
}
function renderFood(some){
  //some => <Food name={some.name}/>
  return <Food key ={some.id} name={some.name} rating = {some.rating}/>
  //name key등props가 내가 가지고 있는 props들을 체크하는 로직도 만들어줘야한다고함.
}
Food.prototype = {
  name: PropTypes.string.isRequired,
  rating: PropTypes.string.isRequired//isrequired가 없으면 error가 나지 않음. 뭐가오든 상관안쓰기때문
};
//Food 컴포넌트에 fav이름에 kimchi라는 value를 가진 프로퍼티를 줌. 이 prop를 function에서 매개변수로 사용가능하다.
//react => 그곳에 쓰는 모든것을 생성함.
export default App; //만든후에 export해야함
 