import React from 'react';
import axios from 'axios';
import PropTypes from 'prop-types';
import Movie from '../components/Movie';
import "./Home.css";

class Home extends React.Component{
    state ={
        isLoading : true,
        movies : []
    };
    getMovies = async() => {
        const {data:{data:{movies}}} =await axios.get("https://yts-proxy.now.sh/list_movies.json?sort_by=rating");
        this.setState({movies , isLoading:false});// 기다린후 실행.
    }
    componentDidMount(){
        // setTimeout(()=>{
        //     this.setState({isLoading: false, someting: true})        
        // },6000)  
        this.getMovies();
    }
    render(){
        const {isLoading , movies} = this.state;
        return <section className="container">{isLoading ? <div className="loader"><span className="loader__text">Loading...</span></div>:
        <div className ="movies">
            {movies.map( movie => 
            <Movie key={movie.id}id={movie.id} title={movie.title} summary={movie.summary} poster={movie.medium_cover_image} year={movie.year} genres={movie.genres}/>
        )}
        </div>
       }

        </section>
    }

}

export default Home; 